body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.header {
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 80%;
  color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}

.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.map {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.location-icon {
  font-size: 1.6rem;
  color: black;
}

.location-icon :hover {
  cursor: pointer;
}

.search-button {
  display: flex;
  justify-content: center;
}

.search-button :hover {
  cursor: pointer;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader img {
  width: 400px;
}

.loader h1 {
  margin-top: -100px;
}

.location-info {
  position: absolute;
  top: 5vh;
  right: 2vw;
  width: 40vw;
  min-height: 10vh;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: whitesmoke;
}

.location-icon ul {
  list-style-type: none;
  padding: 0;
}

.location-icon li {
  padding: 5px 0;
}
